@import "../styles/global.scss";

.randomCodeButton {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 215px;
	height: 52px;
	margin: 13px;
	font-size: 19px;
	border-radius: 30px;
	background: linear-gradient(145deg, #0f0 10%, #004400 90%);
	border: 2px solid $primary-color;
	color: #0f0;
	color: $text-color;
	text-shadow: $logo-color-shadow;
	text-shadow: 0 0 15px $shadow-color;
	font-family: "Courier New", monospace;
	letter-spacing: 1.5px;
	cursor: none;
}

// budgetary - phone low resolution
@include responsive(oppoA98) {
	.randomCodeButton {
        width: 200px;
        height: 50px;
        font-size: 17px;
	}
}

@include responsive(xs-2) {
	.randomCodeButton {
		margin: 14px;
		width: 225px;
		height: 54px;
		font-size: 20px;
	}
}
@include responsive(md-1) {
	.randomCodeButton {
		width: 230px;
	}
}
@include responsive(notebook14){
	.randomCodeButton{
		width: 225px;
		height: 48px;
		font-size: 16px;
		letter-spacing: 1.4px;
		font-weight: 600;
	}
}