@import '../styles/global.scss';

.buttonsContainer {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: start;
	width: 100%;
	height: 360px;
	margin-top: 20px;
	margin-top: 0;
	padding-top: 35px;
	cursor: none;
	// background-color: $test;
}
.btn {
	background-color: transparent;
	border: none;
	cursor: none;
}

@include responsive(xs-1){
	.buttonsContainer{
		margin-top: 10px;
		padding-top: 0;
	}
}
@include responsive(oppoA98){
	.buttonsContainer{
		padding-top: 10px;
	}
}

@include responsive(xs-2){
	.buttonsContainer{	
		margin-top: 50px;	
		padding-top: 20px;
	}
}
@include responsive(md-1){
	.buttonsContainer{	
		margin-top: 60px;	
		padding-top: 30px;
		margin-bottom: 90px;
		height: 380px;
	}
}
@include responsive(md-2){
	.buttonsContainer{	
		margin-bottom: 50px;
		height: 100%;
	}
}
@include responsive(md-3){
	.buttonsContainer{
		height: 400px;
	}
}
@include responsive(notebook14){
	.buttonsContainer{
		margin-top: 120px;
		padding-top: 0;
		height: 320px;
		min-height: 320px;
	}
}

@include responsive(high-lg){
	.buttonsContainer{
		margin-top: 14%;
		background-color: #fff;
	}
}
@media (min-height: 700px){
	.buttonsContainer{
		height: 370px;
		min-height: 370px;
	}
}